body {
  margin: 0;
  padding: 0;
  /* display: flex; */
  /* justify-content: center; */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

conferenceDisplay {
  display: flex;
  flex-direction: row;
  margin: 50px;
  border: 5px solid;
  border-radius: 20px;
}
buttons {
  display: flex;
  justify-content: center;
  margin: 5px;
}
button {
  cursor: pointer;
  width: 45px;
  height: 30px;
  margin-right: 10px;
  border-radius: 10px;
  background-color: white;
  color: #1976d2;
  border-color: #0915ed;
}
conferencelist {
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  margin: 10px;
}
h1 {
  display: flex;
  justify-content: center;
}
h3 {
  display: flex;
  justify-content: center;
}
form {
  display: flex;
  justify-content: center;
}
